import React from 'react'
import TextField from '../reusable-components/TextField'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

class AboutYou extends React.Component {
    constructor(props) {
        super(props)
        this.state = {}
        this.isComplete = this.isComplete.bind(this)
    }

    isComplete() {
        if(this.state['First Name'] && this.state['Last Name'] &&
        this.state['Street'] && 
        this.state['City'] && this.state['State'] && this.state['Zip'] && 
        this.state['Date of Birth']) {
            this.props.nextPage()
            this.props.updateAboutYou()
        }
    }

    render() {
        return (
            <div>
                <Container id="about-you">
                    <Row>
                        <Col><h4 id="about-you-title" className="page-text-header">Tell us about yourself...</h4></Col>
                    </Row>
                    <Row>
                        <Col sm={5} xs={9}><TextField fieldNameHeader="First Name" fieldName="first-name" handleOnBlur={(e)=>this.setState({'First Name': e})}/></Col>
                        {/* <Col sm={5} xs={9}><TextField fieldNameHeader="First Name" fieldName="first-name" handleOnBlur={(e)=>this.setState({'First Name': e}, console.log(e))}/></Col> */}
                        <Col sm={2} xs={3}><TextField fieldNameHeader="MI" fieldName="middle-name" handleOnBlur={(e)=>this.setState({'Middle Name': e})}/></Col>
                        <Col sm={5}><TextField fieldNameHeader="Last Name" fieldName="last-name" handleOnBlur={(e)=>this.setState({'Last Name': e})}/></Col>
                    </Row>

                    <Row>
                        <Col sm={10}><TextField fieldNameHeader="Street Address" fieldName="street-address" handleOnBlur={(e)=>this.setState({'Street': e})}/></Col>
                        <Col sm={2}><TextField fieldNameHeader="Apt/Suite" fieldName="apt" handleOnBlur={(e)=>this.setState({'Apt': e})}/></Col>
                    </Row>

                    <Row>
                        <Col sm={8}><TextField fieldNameHeader="City" fieldName="city" handleOnBlur={(e)=>this.setState({'City': e})}/></Col>
                        <Col sm={2}><TextField fieldNameHeader="State" fieldName="state" handleOnBlur={(e)=>this.setState({'State': e})}/></Col>
                        <Col sm={2}><TextField fieldNameHeader="Zip Code" fieldName="zip-code" handleOnBlur={(e)=>this.setState({'Zip': e})}/></Col>
                    </Row>

                    <Row>
                        <Col sm={4}><TextField fieldNameHeader="Date of Birth" fieldName="date-of-birth" handleOnBlur={(e)=>this.setState({'Date of Birth': e})}/></Col>
                        <Col sm={4}><TextField fieldNameHeader="Email" fieldName="email" handleOnBlur={(e)=>this.setState({'Email': e})}/></Col>
                        <Col sm={4}><TextField fieldNameHeader="Phone" fieldName="phone" handleOnBlur={(e)=>this.setState({'Phone': e})}/></Col>
                    </Row>

                    <Row>
                        <Col sm={8}>{'\u00A0'}</Col>
                        <Col sm={4}><button type="button" className="btn btn-primary btn-next-page" onClick={this.isComplete}>Next: Add Your Vehicles</button></Col>
                    </Row>
                </Container>
            </div>
        )
    }
}

export default AboutYou