import React from 'react'

import AboutYou from './AboutYou'
import VehicleList from './VehicleList';
import DriverList from './DriverList';
import Satisfied from './Satisfied';

class MainContent extends React.Component {
    constructor() {
        super()
        this.state = {
            page: 1,
            aboutYou: {},
            vehicles: [],
            drivers: []
        }
        this.updateAboutYou = this.updateAboutYou.bind(this)
        this.addDriver = this.addDriver.bind(this)
        this.addVehicle = this.addVehicle.bind(this)

        this.nextPage = this.nextPage.bind(this)
        this.previousPage = this.previousPage.bind(this)
      }

      updateAboutYou(aboutYouUpdated) {
        this.setState({aboutYou:aboutYouUpdated})
      }
    
      addDriver(driver) {
        this.setState(prevState => {
            prevState.drivers.push(driver)
            return {
                ...prevState,
                drivers: prevState.drivers
            }
        })
      }
    
      addVehicle(vehicle) {
        this.setState(prevState => {
            prevState.vehicles.push(vehicle)
            return {
                ...prevState,
                vehicles: prevState.vehicles
            }
        })
      }

      nextPage() {
          this.setState(prevState => {
              return {
                  ...prevState,
                  page: prevState.page + 1
              }
          })
      }

      previousPage() {
        this.setState(prevState => {
            return {
                ...prevState,
                page: prevState.page - 1
            }
        })
      }

    render() {
        let mainContent
        if(this.state.page === 1)
            mainContent = <AboutYou updateAboutYou={this.updateAboutYou} nextPage={this.nextPage} previousPage={this.previousPage}/>
        else if(this.state.page === 2)
            mainContent = <VehicleList vehicles={this.state.vehicles} addVehicle={this.addVehicle} nextPage={this.nextPage} previousPage={this.previousPage}/>
        else if(this.state.page === 3)
            mainContent = <DriverList drivers={this.state.drivers} addDriver={this.addDriver} nextPage={this.nextPage} previousPage={this.previousPage}/>
        else if(this.state.page === 4)
            mainContent = <Satisfied />
    
        return (
            mainContent
        )
    }
}

export default MainContent