import React from 'react'
import AddVehicle from './AddVehicle'

import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

import { Button } from 'react-bootstrap'
import { IoIosAddCircleOutline, IoIosCar} from "react-icons/io";

import ModalComponent from '../reusable-components/Modal'
import ModalButton from '../reusable-components/ModalButton'

class ExistingVehicleComponent extends React.Component {
    render() {
        return (
            <Col lg={3} md={4} sm={12}>
                <ModalButton modalButtonText={this.props.vehicle} modalGlyph={<IoIosCar />}/>
            </Col>
        )
    }
}

class AddVehicleComponent extends React.Component {
    constructor(props) {
        super(props)
        this.state = {setVehicleState: false, modalShow: false}
        this.handleAddVehicle = this.handleAddVehicle.bind(this)
        this.updateModalShow = this.updateModalShow.bind(this)
    }

    handleAddVehicle() {
        this.setState(prevState=> {
            return {
                ...prevState,
                setVehicleState: !prevState.setVehicleState
            }
        })
    }

    updateModalShow() {
        this.setState({modalShow: !this.state.modalShow})
    }

    render() {
        return (
            <Col lg={3} md={4} sm={12}>
                <ModalComponent 
                    modalButton={<ModalButton updateModalShow={this.updateModalShow} modalButtonText="Add Vehicle" modalGlyph={<IoIosAddCircleOutline />}/>}
                    modalHeading="Add a Vehicle" 
                    modalcontent={<AddVehicle setVehicleState={this.state.setVehicleState} handleAddVehicle={this.handleAddVehicle} addVehicle={this.props.addVehicle}/>}
                    show={this.state.modalShow}
                    updateModalShow={this.updateModalShow}
                    modalOnAdd={this.handleAddVehicle}
                />
            </Col>
        )
    }
}

class ButtonList extends React.Component {    
    render() {
        const buttonList = [];
        for(let vehicle of this.props.vehicles) {
            buttonList.push(<ExistingVehicleComponent vehicle={vehicle.Name}/>)
        }
        buttonList.push(<AddVehicleComponent addVehicle={this.props.addVehicle}/>)
        return (
            <Row>{buttonList}</Row>
        )
    }
}

class VehicleList extends React.Component {
    render() {
        return (
            <div>
                <Container>
                    <Row>
                        <Col><h4 id="add-vehicle-title" className="page-text-header">Click below to add vehicle to policy</h4></Col>
                    </Row>
                    <ButtonList {...this.props}/>
                    <Row>
                        <Col sm={4}><button type="button" className="btn btn-primary btn-next-page" onClick={this.props.previousPage}>Previous: About You</button></Col>
                        <Col sm={4}>{'\u00A0'}</Col>
                        <Col sm={4}><button type="button" className="btn btn-primary btn-next-page" onClick={this.props.nextPage}>Next: Add Your Drivers</button></Col>
                    </Row>
                </Container>
            </div>
        )
    }
}

export default VehicleList