import React from 'react'
import {Modal, Button, ButtonToolbar} from 'react-bootstrap'

class MyVerticallyCenteredModal extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <Modal
                show={this.props.show}
                onHide={this.props.onHide}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
                >
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter">
                    {this.props.modalHeading}
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {this.props.modalcontent}
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={() => {
                        this.props.onHide()
                        this.props.modalOnAdd()
                    }}>Add</Button>
                    <Button onClick={this.props.onHide}>Close</Button>
                </Modal.Footer>
            </Modal>
        )
    }
}

class ModalComponent extends React.Component {
    render() {
        return (
            <ButtonToolbar>
            {/* <Button variant="light" className="modal-button shadow p-3 bg-white rounded" onClick={() => this.setState({modalShow: true})}> */}
            {this.props.modalButton}
            {/* <Button variant="light" className="modal-button shadow p-3 bg-white rounded" onClick={this.props.updateModalShow}>
                {this.props.modalButton}
            </Button> */}
            <MyVerticallyCenteredModal 
                {...this.props}
                onHide={this.props.updateModalShow}
            />
            </ButtonToolbar>
        )
    }
}

export default ModalComponent;