import React from 'react'
import AddDriver from './AddDriver'

import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'

import { Button } from 'react-bootstrap'
import { IoIosAddCircleOutline, IoIosPerson} from "react-icons/io";

import ModalComponent from '../reusable-components/Modal'
import ModalButton from '../reusable-components/ModalButton'

class ExistingDriverComponent extends React.Component {
    render() {
        return (
            <Col lg={3} md={4} sm={12}>
                <ModalButton className="modal-button shadow p-3 bg-white rounded inactive" modalButtonText={this.props.driver} modalGlyph={<IoIosPerson />}/>
            </Col>
        )
    }
}

class AddDriverComponent extends React.Component {
    constructor(props) {
        super(props)
        this.state = {setDriverState: false, modalShow: false}
        this.handleAddDriver = this.handleAddDriver.bind(this)
        this.updateModalShow = this.updateModalShow.bind(this)
    }

    handleAddDriver() {
        this.setState(prevState=> {
            return {
                ...prevState,
                setDriverState: !prevState.setDriverState
            }
        })
    }

    updateModalShow() {
        this.setState({modalShow: !this.state.modalShow})
    }

    render() {
        return (
            <Col lg={3} md={4} sm={12}>
                <ModalComponent 
                    modalButton={<ModalButton updateModalShow={this.updateModalShow} modalButtonText="Add Driver" modalGlyph={<IoIosAddCircleOutline />}/>}
                    modalHeading="Add a Driver" 
                    modalcontent={<AddDriver setDriverState={this.state.setDriverState} handleAddDriver={this.handleAddDriver} addDriver={this.props.addDriver}/>}
                    show={this.state.modalShow}
                    updateModalShow={this.updateModalShow}
                    modalOnAdd={this.handleAddDriver}
                />
            </Col>
        )
    }
}

class ButtonList extends React.Component {    
    render() {
        const buttonList = [];
        for(let driver of this.props.drivers) {
            buttonList.push(<ExistingDriverComponent driver={driver.Name}/>)
        }
        buttonList.push(<AddDriverComponent addDriver={this.props.addDriver}/>)
        return (
            <Row>{buttonList}</Row>
        )
    }
}


class DriverList extends React.Component {
    render() {
        return (
            <div>
                <Container>
                    <Row>
                        <Col><h4 id="add-vehicle-title" className="page-text-header">Click below to add more drivers to policy</h4></Col>
                    </Row>
                    <ButtonList {...this.props}/>
                    <Row>
                        <Col sm={8}>{'\u00A0'}</Col>
                        <Col sm={4}><button type="button" className="btn btn-primary btn-next-page" onClick={this.props.nextPage}>Submit</button></Col>
                    </Row>
                </Container>
            </div>
        )
    }
}

export default DriverList